import React, { Component } from "react";
import "./App.css";
import "@shopify/polaris/styles.css";
import { Redirect } from "react-router-dom";
import {
  AppProvider,
  Page,
  Card,
  FormLayout,
  Form,
  Button,
  TextField,
  Modal,
  TextContainer,
  Icon,
  ProgressBar,
  Heading,
  Subheading,
  Stack,
  Caption,
  Banner,
  List,
  DropZone,
  Spinner,
  ButtonGroup,
  DatePicker,
  Badge,
  CalloutCard,
  Popover,
  DisplayText,
  Tabs
} from "@shopify/polaris";
import $ from "jquery";
import Recorder from "react-mp3-recorder";
import ReactAudioPlayer from "react-audio-player";
import blobToBuffer from "blob-to-buffer";
import moment from "moment";

class App extends Component {
  defaultState = {
    timer: 900,
    showBanner: false,
    showSuccessBanner: false,
    countDownTime: "00:00",
    featured_discount: "",
    product_collection: "",
    general_announcement: "",
    postText:"",
    last_update: "2019-04-24 4:24:00",
    count: 0,
    active: false,
    audioError: false,
    text: "Collecting Information",
    progress: 100,
    files: [],
    blob: "",
    rejectedFiles: [],
    hasError: false,
    selectedTab: 0,
    audio: "",
    url: "",
    shopID: 0,
    paidStore: true,
    feedTime: "02:30 PM",
    briefDateTime: "Apr 26 2019 02:30 PM",
    month: 3,
    year: 2019,
    popOverActive: false,
    selected: {
      start: new Date("Apr 26 2019 00:00:00 GMT-0500 (EST)"),
      end: new Date("Apr 26 2019 00:00:00 GMT-0500 (EST)")
    }
  };

  constructor(props) {
    super(props);
    //this.handleTabChange = this.handleTabChange.bind(this);
    // Set the default state immediately
    this.state = this.defaultState;

    console.log(this.props);
    console.log(this.props.shopName);
    window.app = this;
  }

  handleTimeChange = field => {
    return value => {
      if (moment(value, "hh:mm a", true).isValid())
        this.setState({
          [field]: value,
          briefDateTime: moment(
            (this.state.selected.start + "").replace("00:00:00", value)
          ).format("MMM DD, YYYY hh:mm a")
        });
    };
  };

  handleDateChange = value => {
    this.setState({
      selected: value,
      briefDateTime: moment(
        (value.start + "").replace("00:00:00", this.state.feedTime)
      ).format("MMM DD, YYYY hh:mm a")
    });
  };

  handleMonthChange = (month, year) => {
    this.setState({
      month,
      year
    });
  };

  togglePopover = (val) => {
    console.log("Clicked");
    this.setState(({ popOverActive }) => {
      return { popOverActive: val };
    });
  };

  PublishedIcon = () => (
    <div
      style={{
        float: "right",
        marginLeft: "10px",
        marginTop: "2px",
        width: "12px",
        height: "12px",
        borderStyle: "solid",
        borderWidth: "0.5px",
        borderColor: "#fff",
        borderRadius: "6px",
        backgroundColor: "Green"
      }}
    />
  );

  convertUTCDateToLocalDate = date => {
    var newDate = new Date(
      date.getTime() + date.getTimezoneOffset() * 60 * 1000
    );

    var offset = date.getTimezoneOffset() / 60;
    var hours = date.getHours();

    newDate.setHours(hours - offset);

    return newDate;
  };

  goForMonthlyPayment = () => {
    var formData = new FormData();

    formData.append("shop_id", this.state.shopID);

    fetch("files/pay.php", {
      method: "POST",
      body: formData
    })
      .then(response => response.json())
      .then(responseJson => {
        window.top.location.href =
          responseJson.recurring_application_charge.confirmation_url;
      })
      .catch(error => {
        this.setState({ message: error });
      });
  };


  handlePostFormSubmit = () => {
    var formData = new FormData();

    formData.append("pub_date", this.state.briefDateTime);
    formData.append("store_id", this.state.shopID);
    formData.append("feed_text", this.state.postText);
    //formData.append("file", this.state.shopID);

    fetch("files/additem.php", {
      method: "POST",
      body: formData
    })
      .then(response => response.json())
      .then(responseJson => {
        console.log(responseJson);
      })
      .catch(error => {
        this.setState({ message: error });
      });
  };

  handleChange = field => {
    return value => this.setState({ [field]: value });
  };

  handleModalChange = () => {
    this.setState(({ active }) => ({ active: !active }));
  };

  handleErrorModalChange = () => {
    this.setState(({ audioError }) => ({ audioError: !audioError }));
  };

  // handleTabChange(_selectedTab) {
  //   this.setState({
  //     ...this.state,
  //     selectedTab: _selectedTab
  //   });
  // }

  pad = (num, size) => {
    var s = num + "";
    while (s.length < size) s = "0" + s;
    return s;
  };

  setContent = type => {
    this.setState({
      ...this.state,
      selectedTab: type == "text" ? 0 : 1
    });
  };

  handleSubmit = event => {
    if (
      this.state.selectedTab == "1" &&
      $.isEmptyObject(this.state.files) &&
      $.isEmptyObject(this.state.blob)
    ) {
      this.setState({
        ...this.state,
        audioError: true
      });
      return false;
    }

    var formData = new FormData();
    if (
      $.isEmptyObject(this.state.files) &&
      !$.isEmptyObject(this.state.blob)
    ) {
      formData.append("file", this.state.blob);
    } else if (
      !$.isEmptyObject(this.state.files) &&
      $.isEmptyObject(this.state.blob)
    ) {
      formData.append("file", this.state.files[0]);
    }
    formData.append("featured_discount", this.state.featured_discount);
    formData.append("product_collection", this.state.product_collection);
    formData.append("general_announcement", this.state.general_announcement);
    formData.append(
      "is_textbriefing",
      this.state.selectedTab == "0" ? "1" : "2"
    );
    formData.append("shop", this.props.shopName);

    fetch("files/data.php", {
      method: "POST",
      body: formData
    })
      .then(response => response.json())
      .then(responseJson => {
        this.setState({
          message: responseJson.message,
          is_textbriefing: responseJson.is_textbriefing,
          active: true,
          timer: 900,
          showBanner: true
        });
        this.startCountdown();
      })
      .catch(error => {
        this.setState({ message: error });
      });
  };

  showBriefingCountDown = () => {
    this.getAppData();
    this.setState({
      showBanner: true,
      timer: 900
    });
    this.startCountdown();
    console.log("Called From Outside");
  };

  startCountdown = () => {
    if (this.timer) clearInterval(this.timer);

    this.timer = setInterval(() => {
      if (this.state.timer - 1 < 1) {
        clearInterval(this.timer);
        this.closeBanner(true);
      }

      this.setState({
        ...this.state,
        timer: this.state.timer - 1,
        countDownTime: this.countDown(this.state.timer - 1)
      });
    }, 1000);
  };

  countDown = time => {
    var minutes = Math.floor(time / 60);
    var seconds = time - minutes * 60;
    return this.pad(minutes, 2) + ":" + this.pad(seconds, 2);
  };

  closeBanner = (showSuccess = false) => {
    //clearInterval(this.timer);
    this.setState({
      showBanner: false,
      showSuccessBanner: showSuccess
    });
  };

  componentDidMount = () => {
    //this.startCountdown();
    this.getAppData();
  };

  getAppData = () => {
    fetch("files/show.php?shop=" + this.props.shopName, {
      method: "GET",
      headers: {
        "Cache-Control": "no-cache, no-store, must-revalidate",
        Pragma: "no-cache",
        Expires: 0
      }
    })
      .then(response => response.json())
      .then(responseJson => {
        if (responseJson.id == "") {
          window.location.href = "intro";
        } else {
          this.setState({
            featured_discount: responseJson.featured_discount,
            product_collection: responseJson.product_collection,
            general_announcement: responseJson.general_announcement,
            last_update: responseJson.last_update,
            selectedTab: responseJson.is_textbriefing == "1" ? 0 : 1,
            is_textbriefing: responseJson.is_textbriefing,
            audio: responseJson.audio,
            progress: responseJson.precentage,
            paidStore: responseJson.paid_store,
            shopID: responseJson.id,
            text: responseJson.textProgress
          });
          if (this.state.progress <= 40) {
            $(".Polaris-ProgressBar__Indicator").addClass("red");
          } else if (this.state.progress <= 70) {
            $(".Polaris-ProgressBar__Indicator").addClass("orange");
          } else {
            $(".Polaris-ProgressBar__Indicator").addClass("green");
          }
        }
      })
      .catch(error => {
        this.setState({ message: error });
      });
    this.setState({
      count: 1
    });
  };

  render() {
    const {
      featured_discount,
      product_collection,
      general_announcement,
      count,
      active,
      audioError,
      showBanner,
      showSuccessBanner,
      progress,
      text,
      audio,
      files,
      hasError,
      rejectedFiles,
      selectedTab,
      is_textbriefing,
      url,
      month,
      year,
      selected,
      feedTime,
      briefDateTime,
      popOverActive,
      postText
    } = this.state;

    const fileUpload = !files.length && <DropZone.FileUpload />;
    const uploadedFiles = files.length > 0 && (
      <Stack vertical>
        {files.map((file, index) => (
          <Stack alignment="center" key={index}>
            <div>
              {file.name} <Caption>{file.size} bytes</Caption>
            </div>
          </Stack>
        ))}
      </Stack>
    );

    const errorMessage = hasError && (
      <Banner
        title="The following images couldn’t be uploaded:"
        status="critical"
      >
        <List type="bullet">
          {rejectedFiles.map((file, index) => (
            <List.Item key={index}>
              {`"${file.name}" is not supported. File type must be .mp3.`}
            </List.Item>
          ))}
        </List>
      </Banner>
    );

    const tabs = [
      {
        id: "tab1",
        content: "Text",
        panelID: "panel2"
      },
      {
        id: "tab2",
        content: "Audio",
        panelID: "panel2"
      }
    ];
    const activator = (
      <TextField
        value={briefDateTime}
        label="Publish Date and Time"
        readOnly={true}
        prefix={<Icon source="calendar" color="black" />}
        onFocus={()=>this.togglePopover(true)}
      />
    );

    const tabPanels = [
      <Tabs.Panel id="panel1">
        <Card sectioned>
          <Form action="" method="post" onSubmit={this.handlePostFormSubmit}>
            <FormLayout>
              <Popover
                active={popOverActive}
                activator={activator}
                onClose={()=>this.togglePopover(false)}
                preferredAlignment="right"
                preferredPosition="below"
                sectioned
              >
                <FormLayout>
                  <Popover.Pane>
                    <DatePicker
                      month={month}
                      year={year}
                      selected={selected}
                      allowRange={false}
                      onChange={this.handleDateChange}
                      onMonthChange={this.handleMonthChange}
                    />
                  </Popover.Pane>
                  <Popover.Pane>
                    <TextField
                      value={feedTime}
                      prefix={<Icon source="calendar" color="black" />}
                      onChange={this.handleTimeChange("feedTime")}
                    />
                  </Popover.Pane>
                </FormLayout>
              </Popover>

              <TextField
                value={postText}
                label="Text"
                multiline={true}
                onChange={this.handleChange("postText")}
              />

              <Button id="BtnTextStyle" submit>
                {" "}
                Save{" "}
              </Button>
            </FormLayout>
          </Form>
        </Card>
      </Tabs.Panel>,
      <Tabs.Panel id="panel2">
        <Card sectioned>
          <Form action="" method="post" onSubmit={this.handleSubmit}>
            <FormLayout>
              <div>
                {!!audio && is_textbriefing == "0" && (
                  <TextContainer>
                    <Heading>Published Audio</Heading>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "center",
                        alignItems: "center",
                        textAlign: "center"
                      }}
                    >
                      <ReactAudioPlayer
                        src={
                          "https://briefify.coldsmoke.co/alex/uploads/" + audio
                        }
                        controls
                        style={{
                          minWidth: "500px"
                        }}
                      />
                    </div>
                  </TextContainer>
                )}
                <TextContainer>
                  <Heading>New Audio</Heading>
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "center",
                      alignItems: "center",
                      textAlign: "center"
                    }}
                  >
                    <div>
                      <br />
                      <br />
                      <Recorder
                        onRecordingComplete={this._onRecordingComplete}
                        onRecordingError={this._onRecordingError}
                        style={{
                          margin: "0 auto"
                        }}
                      />
                      <p>Click and hold to start recording mp3.</p>
                      {url && (
                        <div>
                          <ReactAudioPlayer
                            src={url}
                            controls
                            style={{
                              minWidth: "500px"
                            }}
                          />
                        </div>
                      )}
                      <br />
                      <br />
                      <strong>OR</strong>
                      <br />
                      <br />
                    </div>
                  </div>
                </TextContainer>
              </div>
              <Stack vertical>
                {errorMessage}
                <DropZone
                  accept="audio/mp3"
                  type="file"
                  onDrop={(files, acceptedFiles, rejectedFiles) => {
                    this.setState({
                      files: [...this.state.files, ...acceptedFiles],
                      rejectedFiles: rejectedFiles,
                      hasError: rejectedFiles.length > 0,
                      blob: ""
                    });
                  }}
                >
                  {uploadedFiles}
                  {fileUpload}
                </DropZone>
              </Stack>

              <p>
                <strong>Note :</strong> Please see the{" "}
                <a
                  rel="noopener noreferrer"
                  href="https://developer.amazon.com/docs/flashbriefing/flash-briefing-skill-api-feed-reference.html#cert-audio-content"
                  target="_blank"
                >
                  link
                </a>{" "}
                and go through the guide before uploading audio content in alexa
                skill{" "}
              </p>
              <Button id="BtnAudioStyle" submit>
                Save
              </Button>
            </FormLayout>
          </Form>
        </Card>
      </Tabs.Panel>
    ];

    if (this.state.count === 0) {
    }

    return (
      <AppProvider>
        {!!this.state.progress && this.state.progress > 0 ? (
          <Page>
            <Modal
              open={active}
              size="Medium"
              onClose={this.handleModalChange}
              title="Alexa Submission "
              primaryAction={{
                content: "OK",
                onAction: this.handleModalChange
              }}
            >
              <Modal.Section>
                <TextContainer>
                  <Icon source="save" color="green" />
                  <p className="Savetext">
                    Your Alexa Briefing is successfully saved.{" "}
                  </p>
                </TextContainer>
              </Modal.Section>
            </Modal>
            <Card sectioned>
              <Heading h1>App Submission Progress</Heading>
              <Subheading>
                {this.state.text} - {this.state.progress}% Completed
              </Subheading>
              <ProgressBar progress={this.state.progress} size="large" />
            </Card>
            {showBanner && (
              <Banner title="Briefing Publishing" status={"warning"}>
                <p>
                  Flash briefing will be available on alexa in{" "}
                  {this.state.countDownTime} mins
                </p>
              </Banner>
            )}
            {showSuccessBanner && (
              <Banner
                title="Briefing Published"
                onDismiss={() => {
                  this.setState({
                    ...this.state,
                    showSuccessBanner: false
                  });
                }}
                status={"success"}
              />
            )}
            {!this.state.paidStore && (
              <CalloutCard
                title="Update to Pro"
                illustration="https://cdn.shopify.com/s/assets/admin/checkout/settings-customizecart-705f57c725ac05be5a34ec20c05b94298cb8afd10aac7bd9c7ad02030f48cfa0.svg"
                primaryAction={{
                  content: "Upgrade to Pro",
                  onAction: this.goForMonthlyPayment
                }}
              >
                <p>Upload or Record audio for alexa</p>
              </CalloutCard>
            )}
            <Card title="Briefing Types " sectioned="true">
              <ButtonGroup segmented>
                <Button
                  primary={selectedTab == 0 ? true : false}
                  onClick={() => this.setContent("text")}
                >
                  <p>
                    Text {is_textbriefing == "1" && <this.PublishedIcon />}{" "}
                    {!this.state.formErrorMessage}
                  </p>
                </Button>
                {this.state.paidStore && (
                  <Button
                    primary={selectedTab == 1 ? true : false}
                    onClick={() => this.setContent("audio")}
                  >
                    <p>
                      Audio {is_textbriefing == "0" && <this.PublishedIcon />}
                    </p>
                  </Button>
                )}
              </ButtonGroup>
              {this.state.last_update !== "0000-00-00 00:00:00" && (
                <p>
                  <br />
                  Last updated on{" "}
                  {this.convertUTCDateToLocalDate(
                    new Date(this.state.last_update)
                  ).toLocaleString()}
                </p>
              )}
            </Card>

            {tabPanels[selectedTab]}

            {audioError && (
              <Modal
                open={audioError}
                size="Medium"
                onClose={this.handleErrorModalChange}
                title="Alexa Submission "
                primaryAction={{
                  content: "OK",
                  onAction: this.handleErrorModalChange
                }}
              >
                <Modal.Section>
                  <TextContainer>
                    <Icon source="save" color="red" />
                    <p className="errortext">Please upload or record audio</p>
                  </TextContainer>
                </Modal.Section>
              </Modal>
            )}
          </Page>
        ) : (
          <div style={{ textAlign: "center", marginTop: "100px" }}>
            <Page>
              <Spinner size="large" color="teal" />
            </Page>
          </div>
        )}
      </AppProvider>
    );
  }

  _onRecordingComplete = blob => {
    blobToBuffer(blob, (err, buffer) => {
      if (err) {
        console.error(err);
        return;
      }
      this.setState({ blob: blob, files: [] });
      if (this.state.url) {
        window.URL.revokeObjectURL(this.state.url);
      }

      this.setState({
        url: window.URL.createObjectURL(blob)
      });
    });
  };

  _onRecordingError = err => {
    console.log("error recording", err.message);

    var isChrome = !!window.chrome;

    if (isChrome) {
      console.log("Open external");
      window.open("/alex/external/record", "_blank");
    }
    console.log(err.code, typeof err, err.name);

    if (this.state.url) {
      window.URL.revokeObjectURL(this.state.url);
    }

    this.setState({ url: null });

    console.log(err + " == DOMException");
  };
}

export default App;
